<template>
    <div class="main">
        <transition name="slide-fade">
            <div id="loading" v-if="loading">
                <Loader :size="'20vh'" :color="'rgb(59, 129, 255)'"></Loader>
            </div>
        </transition>
        <router-link to="/">
            <h1 id="title">I Music</h1>
        </router-link>
        <div class="line"></div>
        <form @submit="registerServer()">
            <input v-model="username" type="text" id="username" class="text_box" placeholder="ID" minlength="4"
                maxlength="20" required>
            <input v-model="password" type="password" id="password" class="text_box" placeholder="PASSWORD" minlength="4"
                maxlength="20" required>
            <input v-model="email" type="email" id="email" class="text_box" placeholder="EMAIL" maxlength="40" required>
            <button id="register" class="def_btn" type="submit">회원가입</button>
        </form>
    </div>
</template>

<script>
    import axios from 'axios';
    import Loader from 'vue-spinner/src/BounceLoader.vue'
    import connect from '../assets/connect'
    var mainPath = connect.tUrl

    export default {
        name: 'login_p',
        data() {
            return {
                username: "",
                password: "",
                email: "",
                loading: false
            }
        },
        components: {
            Loader
        },
        methods: {
            registerServer() {
                this.loading = true;
                axios.post(mainPath + "/auth/register", {
                    username: this.username,
                    password: this.password,
                    email: this.email
                })
                    .then(data => {
                        this.loading = false;
                        this.$router.push("/")
                        alert("회원가입이 성공적으로 처리되었습니다.");
                    })
                    .catch(err => {
                        this.loading = false;
                        alert("이미 있는 유저이름 입니다.");
                    });
            }
        }
    }
</script>


<style scoped>
    #title {
        margin: 8% 0 20px 0;
    }

    .main {
        display: flex;
        flex-flow: column;
        flex-wrap: wrap;
        align-items: center;
    }

    .line {
        width: 600px;
        height: 2px;

        margin: 10px;

        background-color: rgb(224, 224, 224);
        border-radius: 50px;
    }

    .lost_password {
        width: 22%;
        color: rgb(200, 200, 200);

        margin-bottom: 3%;
    }

    .text_box {
        margin: 15px;
    }

    #register {
        background-color: rgb(255, 190, 100);
    }
</style>


