<template>
    <div class="main">
        <nav>
            <router-link to="/" class="title">
                <h1 id="s_title">I Music</h1>
            </router-link>
        </nav>
        <div id="search">
            <input v-model="search_data" type="text" id="research" placeholder="검색" @change="gotoSearch()">
            <ul>
                <li v-for="item in res_data"><img class="y_logo" :src="item.thumbnails.default.url" height="100%">
                    {{ item.title }} <i class="material-icons like" :class="getHeart(item.like)" @click="toggleLike(item)">favorite</i>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import axios from 'axios';
    import Loader from 'vue-spinner/src/BounceLoader.vue'
    import search from '../assets/search'
    import connect from '../assets/connect'
    var mainPath = connect.tUrl

    export default {
        name: 'search_p',
        created() {
            this.search_data = search.searchTarget
            this.gotoSearch()
        },
        data() {
            return {
                search_data: "",
                count: 0,
                res_data: []
            }
        },
        components: {
            Loader
        },
        methods: {
            toggleLike(data) {
                var index = this.res_data.findIndex(x => x.id == data.id)
                var router = (this.res_data[index].like ? 'delete/' : 'create/')
                axios.get(mainPath + "/auth/list/" + router + this.res_data[index].id)
                .then(data => {
                    this.gotoSearch()
                })
                .catch(err => {
                    this.gotoSearch()
                })
            },
            getHeart(like) {
                return (like ? "allike" : "")
            },
            gotoSearch() {
                search.setSearchTarget(this.search_data)
                axios.get(mainPath + "/search/" + this.search_data)
                    .then(data => {
                        this.res_data = data.data
                    })
                    .catch(err => {
                        console.log(err)
                    })
            }
        }
    }
</script>


<style scoped>
    ul {
        display: flex;
        flex-flow: column;
        align-items: center;

        width: 100%;
        height: 100%;

        margin: 0;
        padding: 0;
        list-style: none;
    }

    li {
        position: relative;
        display: flex;
        align-items: center;

        width: 90%;
        height: 80px;

        font-size: 25px;
        white-space: normal;
        overflow: hidden;

        transition: 0.5s;
    }

    li:hover {
        background-color: rgba(59, 129, 255, 0.3);
    }

    .y_logo {
        height: 80%;
        margin: 0 15px;

        border-radius: 10px;
    }

    nav {
        display: flex;
        align-items: center;

        position: fixed;

        width: 100%;
        height: 80px;

        box-shadow: 0px 1px 1px rgb(59, 129, 255);
        ;
        background-color: white;

        z-index: 100;
    }

    .title {
        margin-left: 5%;
        text-align: center;
    }

    #s_title {
        font-size: 40px;

        width: 5em;

        color: rgb(59, 129, 255);

        transition: 0.5s;
    }

    #search {
        display: flex;
        flex-flow: column;
        align-items: center;

        position: fixed;

        right: 0;

        width: 100%;
        height: 100vh;

        padding-top: 80px;

        background-color: white;
    }

    #research {
        width: 60%;
        height: 15px;

        margin: 40px;
        padding: 15px;

        border: 1px solid rgb(224, 224, 224);
        border-radius: 10px;

        font-size: 20px;
    }

    i.like {
        cursor: pointer;

        position: absolute;
        right: 10%;
        font-size: 60px;

        transition: 0.5s;

        color: rgba(255, 0, 0, 0.1)
    }

    i.like:hover:not(.allike) {
        color: rgba(255, 0, 0, 0.2)
    }

    i.allike {
        color: rgba(255, 0, 0, 0.5)
    }
</style>


